html, body {
  padding: 0;
  margin: 0; }

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: Inter, Arial, Helvetica, sans-serif; }

main {
  flex-grow: 1; }

.page-header {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 24px; }
  .page-header .brand {
    font-size: 24px; }
  .page-header a {
    color: white;
    text-decoration: none; }
    .page-header a:hover, .page-header a:focus {
      opacity: 0.7; }

.page-footer {
  text-align: center;
  padding: 8px 0; }

[draggable] {
  user-select: none;
  -khtml-user-drag: element;
  -webkit-user-drag: element; }

.tasklist-wrapper {
  min-width: 350px;
  width: max-content;
  margin: 0 auto; }
  .tasklist-wrapper #tasklist {
    list-style-type: none;
    padding: 0; }
  .tasklist-wrapper .btn-add-task {
    background-color: black;
    color: white;
    border: transparent;
    padding: 4px 12px;
    border-radius: 2px;
    float: right;
    cursor: pointer; }
    .tasklist-wrapper .btn-add-task:focus, .tasklist-wrapper .btn-add-task:hover {
      opacity: 0.7; }

.task {
  padding: 8px 4px;
  margin: 8px 0; }
  .task--ghost {
    opacity: 0.5; }
  .task--drag {
    border-radius: 4px;
    border: 1px solid grey; }
  .task__btn-wrapper {
    float: right; }
    .task__btn-wrapper a {
      display: inline-block;
      font-size: 18px;
      text-decoration: none;
      color: black; }
      .task__btn-wrapper a:hover, .task__btn-wrapper a:focus {
        color: #036433; }
  .task__checkbox:checked + .task__label {
    text-decoration: line-through; }
  .task__label {
    padding: 2px 4px; }
    .task__label:focus {
      outline: none;
      background-color: #ffffd4; }
